<template>
  <div
    v-if="user"
    id="user-invite"
  >
    <div class="container">
      <h3 class="sub-title">邀请码{{ created ? '（长按图片可保存到手机相册）' : '' }}</h3>
    </div>
    <div class="capture-wrap">
      <img
        v-if="result"
        :src="result">
      <div
        v-else
        class="capture-area"
      >
        <div class="main">
          <div class="border-outline">
            <div class="avatar-wrap">
              <div class="avatar-bg"/>
              <div class="avatar">
                <div class="avatar-hat"/>
                <img
                  :src="$resize(user.avatar, { width: 200 })"
                  crossorigin="anonymous"
                >
              </div>
            </div>
            <p class="nickname">我是<strong>{{ user.nickname }}</strong></p>
            <p class="info">和我一起在 <strong>calibur.tv</strong> 追番吧</p>
            <div class="tags">
              <div
                v-for="(tag, index) in tags"
                :key="index"
                class="badge"
              >
                <img src="~assets/img/invite/icon.png">
                <span>{{ tag }}</span>
              </div>
            </div>
            <div class="line"/>
            <div class="intro">快扫码注册(๑˃́ꇴ˂̀๑)，邀请者有<span>团子奖励</span></div>
            <div class="logo"/>
            <div
              ref="qr"
              class="qr"
            />
            <div class="slogan"/>
            <div class="snow-left"/>
            <div class="snow-right"/>
            <div class="border-out"/>
            <div class="border-inner"/>
          </div>
        </div>
        <div class="top-bg"/>
        <div class="bottom-bg"/>
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'
import serverAuth from '~/mixins/serverAuth'

export default {
  name: 'PageUserInvite',
  mixins: [serverAuth],
  data() {
    return {
      created: false,
      result: '',
      cartoons: [
        '喰种',
        '死亡笔记',
        '刀剑神域',
        '一拳超人',
        '巨人',
        '战栗杀机',
        'JOJO'
      ]
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
    link() {
      return this.user ? `http://calibur.tv/about/invite/${this.user.id}` : ''
    },
    tags() {
      return this.$utils.shuffle(this.cartoons).slice(0, 5)
    }
  },
  mounted() {
    if (!this.user) {
      this.$toast.error('请先登录').then(() => {
        window.location.href = '/'
      })
      return
    }
    this.$nextTick(() => {
      this.generateQrCode()
    })
  },
  methods: {
    generateQrCode() {
      import('~/assets/js/qrcode').then(ESModule => {
        const QRCode = ESModule.default
        new QRCode(this.$refs.qr, {
          text: this.link,
          width: 360,
          height: 360
        })
        setTimeout(() => {
          this.create()
        }, 3000)
      })
    },
    create() {
      html2canvas(document.querySelector('.capture-area'), {
        allowTaint: false,
        useCORS: true,
        backgroundColor: null
      })
        .then(canvas => {
          this.result = canvas.toDataURL('image/png')
          this.created = true
          this.$toast.success('长按图片保存')
        })
        .catch(() => {})
    }
  }
}
</script>
